import { resetAll, resetGlobal, getGlobal } from "./Data.fs.js";
import { REPO_DIALOG } from "./Dialog.fs.js";
import { FSharpMap__get_Item, toList, containsKey } from "./.fable/fable-library.3.1.11/Map.js";
import { map } from "./.fable/fable-library.3.1.11/List.js";
import { toConsole, printf, toText } from "./.fable/fable-library.3.1.11/String.js";
import { toString } from "./.fable/fable-library.3.1.11/Types.js";

export function lookupCurrentDialogWindow(s) {
    const reference = s.UI.fields[0].Reference;
    const dialog = getGlobal(REPO_DIALOG, reference.D);
    if (!containsKey(reference.W, dialog.DialogWindows)) {
        let message;
        const arg30 = map((tuple) => tuple[0], toList(dialog.DialogWindows));
        message = toText(printf("Reference error: Window \u003c%s\u003e not found in Dialog \u003c%s\u003e, found: %A"))(reference.W)(reference.D)(arg30);
        throw (new Error(message));
    }
    return FSharpMap__get_Item(dialog.DialogWindows, reference.W);
}

export function executeCurrentDialogWindow(s) {
    const matchValue = lookupCurrentDialogWindow(s).OnEntry;
    if (matchValue != null) {
        return matchValue(s);
    }
    else {
        return s;
    }
}

export function execute(a) {
    return (arg) => executeCurrentDialogWindow(a.Exec(arg));
}

let _gameToLoadOption = void 0;

export function setGameRunner(game) {
    if (_gameToLoadOption != null) {
        throw (new Error("Trying to load game when game was already loaded: " + toString(game)));
    }
    else {
        _gameToLoadOption = game;
        toConsole(printf("loading game..."));
        game.Build();
    }
}

export function getGameRunner() {
    if (_gameToLoadOption != null) {
        return _gameToLoadOption;
    }
    else {
        throw (new Error("no game to load"));
    }
}

export function reset() {
    resetGlobal(REPO_DIALOG);
    resetAll();
}

