import { toString as toString_1, Union } from "./.fable/fable-library.3.1.11/Types.js";
import { union_type, string_type } from "./.fable/fable-library.3.1.11/Reflection.js";
import { fold as fold_1, singleton, empty, tail as tail_1, head, isEmpty, cons, reverse } from "./.fable/fable-library.3.1.11/List.js";
import { map, fold } from "./.fable/fable-library.3.1.11/Seq.js";
import { split, substring, endsWith } from "./.fable/fable-library.3.1.11/String.js";
import { FSharpMap__get_Item, containsKey } from "./.fable/fable-library.3.1.11/Map.js";

export class RichTextElement extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Text", "Bold", "HighlightedAsValue"];
    }
}

export function RichTextElement$reflection() {
    return union_type("RichText.RichTextElement", [], RichTextElement, () => [[["Item", string_type]], [["Item", string_type]], [["Item", string_type]]]);
}

export function parse(txt, state) {
    return reverse(fold((acc, el_1) => {
        if (el_1.tag === 0) {
            let pattern_matching_result, t, tail;
            if (!isEmpty(acc)) {
                if (head(acc).tag === 0) {
                    pattern_matching_result = 0;
                    t = head(acc).fields[0];
                    tail = tail_1(acc);
                }
                else {
                    pattern_matching_result = 1;
                }
            }
            else {
                pattern_matching_result = 1;
            }
            switch (pattern_matching_result) {
                case 0: {
                    return cons(new RichTextElement(0, t + el_1.fields[0]), tail);
                }
                case 1: {
                    return cons(el_1, acc);
                }
            }
        }
        else {
            return cons(el_1, acc);
        }
    }, empty(), map((el) => {
        let s_1;
        if (s_1 = el, (s_1.indexOf("(") === 0) ? endsWith(s_1, ")") : false) {
            const sWithBraces = el;
            const s = substring(sWithBraces, 1, sWithBraces.length - 2);
            if (containsKey(s, state.Data)) {
                return new RichTextElement(2, toString_1(FSharpMap__get_Item(state.Data, s)));
            }
            else {
                return new RichTextElement(1, s);
            }
        }
        else {
            return new RichTextElement(0, el);
        }
    }, split(txt, ["*"], null, 0))));
}

export function text(txt) {
    return singleton(new RichTextElement(0, txt));
}

export function bold(txt) {
    return new RichTextElement(1, txt);
}

export function toString(rt) {
    return fold_1((acc, el) => ((el.tag === 2) ? (acc + el.fields[0]) : ((el.tag === 1) ? (((acc + "*") + el.fields[0]) + "*") : (acc + el.fields[0]))), "", rt);
}

