import { split } from "./.fable/fable-library.3.1.11/String.js";
import { equalsWith } from "./.fable/fable-library.3.1.11/Array.js";
import { randomNext, comparePrimitives } from "./.fable/fable-library.3.1.11/Util.js";
import { length, item } from "./.fable/fable-library.3.1.11/List.js";

export function splitByDot2(str) {
    const matchValue = split(str, ["."], null, 0);
    if ((!equalsWith((x, y) => comparePrimitives(x, y), matchValue, null)) ? (matchValue.length === 2) : false) {
        return [matchValue[0], matchValue[1]];
    }
    else {
        const message = ("cannot split string into 2 by dot: \u0027" + str) + "\u0027";
        throw (new Error(message));
    }
}

export const random = {};

export function randomOfList(lst) {
    return item(randomNext(0, length(lst)), lst);
}

